export const getNumber = (str) => {
  return +str.toString().replace(/\D/g, "");
};

export const validateNumberString = (value) => {
  return value
    .toFixed(0)
    .toString()
    .split("")
    .reduce(
      (a, e, i) => a + e + (i !== value.length - 1 && i % 3 === 2 ? " " : ""),
      ""
    );
};
