<template>
  <section class="offer-create">
    <!-- title -->
    <h1 class="offer-create__title page__title">
      Коммерческое предложение <span v-if="edit && offer">№{{ offer.id }}</span>
      <span class="offer-create__title-date" v-if="edit && offer">{{
        offerDate
      }}</span>
    </h1>
    <div class="offer-create-wrapper page-wrapper">
      <!-- fields -->
      <div class="offer-create-fields fields">
        <div class="offer-create-fields-row fields-row">
          <base-input
            :label="'Контрагент'"
            :id="'offerCreatecounterparty'"
            :suggestions="counterparties"
            :search="true"
            @setSuggestion="getCounterparty"
            @keypressEnter="getCounterparty"
            @input="getCounterparties"
            v-model="data.counterparty"
          />
        </div>
        <div class="offer-create-fields-row fields-row">
          <base-input
            :label="'ИНН'"
            :id="'offerCreateInn'"
            :disbled="true"
            :disabled="data.inn && true"
            :readonly="data.inn && true"
            v-model="data.inn"
          />
        </div>
        <div class="offer-create-fields-row fields-row _area">
          <base-textarea
            :label="'Адрес'"
            :id="'offerCreateAddress'"
            :disabled="data.address && true"
            :readonly="data.address && true"
            v-model="data.address"
          />
        </div>
        <div class="offer-create-fields-row fields-row">
          <base-input
            :label="'Телефон'"
            :id="'offerCreatePhone'"
            :disabled="data.phone && true"
            :readonly="data.phone && true"
            v-model="data.phone"
          />
        </div>
        <div class="offer-create-fields-row fields-row">
          <base-input
            :label="'Электронная почта'"
            :id="'offerCreateEmail'"
            :disabled="data.email && true"
            :readonly="data.email && true"
            v-model="data.email"
          />
        </div>
        <div class="offer-create-fields-row fields-row">
          <base-input
            :label="'Контактное лицо'"
            :id="'offerCreateContact'"
            :disabled="data.contactPerson && true"
            :readonly="data.contactPerson && true"
            v-model="data.contactPerson"
          />
        </div>
        <div class="offer-create-fields-row fields-row _validy">
          <base-input
            :label="'Срок действия'"
            :short="true"
            :id="'offerCreateValidy'"
            :disabled="data.validy && true"
            :readonly="data.validy && true"
            v-model="data.validy"
          />
        </div>
        <div class="offer-create-fields-row fields-row _area">
          <base-input
            :label="'Конечный заказчик'"
            :id="'offerCreateEndCustomer'"
            :suggestions="customers"
            :search="true"
            @setSuggestion="setCustomer"
            @keypressEnter="setCustomer"
            @input="getEndCustomer"
            v-model="data.endCustomer"
          />
        </div>
        <div class="offer-create-fields-row fields-row">
          <base-input
            :label="'Регион'"
            :id="'offerCreateRegion'"
            :disabled="data.region && true"
            :readonly="data.region && true"
            v-model="data.region"
          />
        </div>
      </div>
      <!-- products -->
      <div class="offer-create-products">
        <h2 class="offer-create-products__title">Продукция</h2>
        <div class="offer-create-products-row article-row">
          <div class="offer-create-products-field article-row-field">
            <base-input
              :id="'offerCreatecounterpartyProduct'"
              :placeholder="'Артикул или наименование'"
              :search="true"
              :suggestions="productSuggestions"
              @setSuggestion="getProducts"
              @keypressEnter="getProducts"
              @input="getProducts('search')"
              v-model="productsSearch"
            />
          </div>
          <div class="offer-create-products-add article-row-button">
            <base-button :type="'outlined'" @clicked="addProduct">
              Добавить
            </base-button>
          </div>
        </div>
        <!-- products table -->
        <offer-create-table
          :products="products"
          @editProduct="editProduct"
          @removeProduct="removeProduct"
        />
      </div>
      <!-- company -->
      <div class="offer-create-company" :class="{ filled: products.length }">
        <h3 class="offer-create-company__title">Выбрать компанию</h3>
        <div class="offer-create-company__select">
          <base-select
            :options="companies"
            v-model="data.company"
            :borderOnly="true"
          />
        </div>
      </div>
      <!-- type -->
      <div class="offer-create-type">
        <h4 class="offer-create-type__title">Сохранить</h4>
        <div class="offer-create-types">
          <div class="base-check">
            <input
              type="radio"
              id="offerCreatePdf"
              value="pdf"
              v-model="data.tech_docs_format"
            />
            <label for="offerCreatePdf" class="upp"> pdf </label>
          </div>
          <div class="base-check _excel">
            <input
              type="radio"
              id="offerCreateExcel"
              value="excel"
              v-model="data.tech_docs_format"
            />
            <label for="offerCreateExcel"> Excel </label>
          </div>
        </div>
      </div>
      <!-- type -->
      <div class="offer-create-type _get-ready">
        <h4 class="offer-create-type__title _get-ready">Подготовить</h4>
        <div class="offer-create-types _get-ready">
          <div class="base-check">
            <input type="checkbox" id="offerReadyTz" v-model="data.tz" />
            <label for="offerReadyTz" class="upp"> тз </label>
          </div>
          <div class="base-check upp _ru">
            <input type="checkbox" id="offerReadyRu" v-model="data.ru" />
            <label for="offerReadyRu" class="upp"> ру </label>
          </div>
        </div>
        <div class="offer-create-tzType" v-if="data.tz">
          <h5>Тип</h5>
          <div>
            <div class="base-check radio">
              <input
                type="radio"
                id="offerCreateKtru"
                value="ktru"
                v-model="data.tzType"
              />
              <label for="offerCreateKtru" class="upp"> ктру </label>
            </div>
            <div class="base-check radio">
              <input
                type="radio"
                id="offerCreateTu"
                value="tu"
                v-model="data.tzType"
              />
              <label for="offerCreateTu" class="upp"> ту </label>
            </div>
            <div class="base-check radio">
              <input
                type="radio"
                id="offerCreateKtrus"
                value="ktrus"
                v-model="data.tzType"
              />
              <label for="offerCreateKtrus">
                <span class="upp">ктру</span> сокр.
              </label>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- actions -->
    <div class="offer-create-actions page-buttons">
      <div class="offer-create__send">
        <base-button :type="'filled'" @clicked="sendToEmail">
          Отправить
        </base-button>
      </div>
      <div class="offer-create__save">
        <base-button :type="'outlined'" @clicked="saveOffer">
          Сохранить
        </base-button>
      </div>
      <div class="offer-create__dl">
        <base-button :type="'outlined'"> Скачать </base-button>
      </div>
    </div>
  </section>
  <base-modal :open="openModal" :text="modalText" @close="openModal = false" />
</template>

<script>
import { useMeta } from "vue-meta";
import BaseTextarea from "@/components/common/Base.Textarea.vue";
import BaseSelect from "@/components/common/Base.Select.vue";
import OfferCreateTable from "./components/OfferCreateTable.vue";

export default {
  components: {
    BaseTextarea,
    BaseSelect,
    OfferCreateTable,
  },
  computed: {
    offerDate() {
      const date = new Date(this.offer.created_at);
      const day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      const week = date.toLocaleString("ru", { month: "long" });
      const year = date.getFullYear();
      return `${day} ${week} ${year}`;
    },
    edit() {
      return this.$route.query.id ? true : false;
    },
  },
  data() {
    return {
      offer: null,
      data: {
        counterparty: "",
        inn: "",
        address: "",
        phone: "",
        email: "",
        contactPerson: "",
        validy: "",
        endCustomer: "",
        region: "",
        tech_docs_format: "",
        tz: false,
        ru: false,
        tzType: "",
        ready: "",
      },
      openModal: false,
      modalText: "Отправлено",
      products: [],
      productsSearch: "",
      companies: [
        {
          key: "Не Выбран",
          value: "",
        },
      ],
      company: "",
      counterparties: [],
      productSuggestions: [],
      currentCounterparty: {},
      customers: [],
      currentCustomer: {},
      currentProduct: "",
    };
  },
  setup() {
    useMeta({ title: "Страница Коммерческого предложения" });
  },
  mounted() {
    this.edit && this.getOffer();
    this.getCompanies();
  },
  methods: {
    getCompanies() {
      this.$api.get(`${this.$api.defaults.API}/company`).then((res) => {
        for (let c of res.data.results) {
          const company = {
            key: c.name,
            value: c.id,
          };
          !this.companies.some((i) => i.value == company.value) &&
            company.key &&
            this.companies.push(company);
        }
      });
    },
    getOffer() {
      this.$api
        .get(`${this.$api.defaults.API}/offer/${this.$route.query.id}`)
        .then((res) => {
          console.log(res.data);
          this.offer = res.data;
          this.currentCounterparty = this.offer.counterparty || "";
          this.setCounterparty();
          this.data.endCustomer =
            (this.offer.end_customer && this.offer.end_customer.name) || "";
          this.data.region =
            (this.offer.end_customer && this.offer.end_customer.region) || "";
          this.currentCustomer = this.offer.end_customer;
          this.products = this.offer.offer_products || "";
          this.data.company = res.data.company.id || "";
          this.data.tech_docs_format = this.offer.tech_docs_format;
        });
    },
    getProducts(type = "") {
      const params = {
        [this.productsSearch && "search"]: this.productsSearch,
      };
      this.$api
        .get(`${this.$api.defaults.API}/product`, { params })
        .then((res) => {
          this.productSuggestions = [];
          for (const p of res.data.results) {
            if (type !== "search") this.getProduct();
            if (type == "search") {
              // get search suggestions
              if (this.productsSearch) {
                this.productSuggestions.push({
                  key: p.name,
                  value: p.id,
                });
              }
            }
          }
        });
    },
    getProduct() {
      this.$api
        .get(`${this.$api.defaults.API}/product/${this.productsSearch}`)
        .then((res) => {
          console.log(res.data);
          const product = {
            product: res.data.id,
            price: `${res.data.price}`,
            discount: this.offer.counterparty.discount,
            count: 1,
            cost: `${res.data.price}`,
          };
          this.currentProduct = product;
        });
    },
    getCounterparties() {
      const params = {
        [this.data.counterparty && "search"]: this.data.counterparty,
      };
      this.counterparties = [];
      this.$api
        .get(`${this.$api.defaults.API}/counterparty`, { params })
        .then((res) => {
          for (const p of res.data.results) {
            // get search suggestions
            const counterparty = {
              key: p.name || "",
              value: p.id || "",
            };
            if (
              !this.counterparties.some(
                (i) => i.value === counterparty.value
              ) &&
              counterparty.key
            ) {
              this.counterparties.push(counterparty);
            }
          }
        });
    },
    getCounterparty() {
      this.$api
        .get(`${this.$api.defaults.API}/counterparty/${this.data.counterparty}`)
        .then((res) => {
          this.currentCounterparty = res.data;
          this.setCounterparty();
        });
    },
    setCounterparty() {
      this.data.counterparty = this.currentCounterparty.name || "";
      this.data.inn = this.currentCounterparty.inn || "";
      this.data.address = `${
        this.currentCounterparty.address_street &&
        this.currentCounterparty.address_street + ","
      } ${
        this.currentCounterparty.address_city &&
        this.currentCounterparty.address_city + ","
      } ${
        this.currentCounterparty.address_country &&
        this.currentCounterparty.address_country + ","
      } ${
        this.currentCounterparty.address_index &&
        this.currentCounterparty.address_index
      }`;
      this.data.phone = `+${this.currentCounterparty.phone_number}`;
      this.data.email = this.currentCounterparty.email;
      this.data.contactPerson = this.currentCounterparty.contact_person || "";
    },
    getEndCustomer() {
      const params = {
        [this.data.endCustomer && "search"]: this.data.endCustomer,
      };
      this.customers = [];
      this.$api
        .get(`${this.$api.defaults.API}/end-customer`, { params })
        .then((res) => {
          console.log(res.data);
          for (const c of res.data.results) {
            // get search suggestions
            const customer = {
              key: c.name || "",
              value: c.id || "",
            };
            if (
              !this.customers.some((i) => i.value === customer.value) &&
              customer.key
            ) {
              this.customers.push(customer);
            }
          }
        });
    },
    setCustomer() {
      if (this.data.endCustomer) {
        this.$api
          .get(
            `${this.$api.defaults.API}/end-customer/${this.data.endCustomer}`
          )
          .then((res) => {
            this.currentCustomer = res.data;
            this.data.endCustomer = this.currentCustomer.name || "";
            this.data.region = this.currentCustomer.region || "";
          });
      }
    },
    addProduct() {
      this.$api
        .post(
          `${this.$api.defaults.API}/offer/${this.offer.id}/product`,
          this.currentProduct
        )
        .then((res) => {
          console.log(res.data);
          this.offer.offer_products.push(res.data);
        });
    },
    editProduct(id) {
      const index = this.products.findIndex((i) => i.id === id);
      const data = {
        product: this.products[index].product[index],
        price: this.products[index].price,
        discount: this.products[index].discount,
        count: this.products[index].count,
        cost: this.products[index].cost,
      };
      console.log(data);
      this.$api
        .patch(
          `${this.$api.defaults.API}/offer/${this.offer.id}/product/${id}`,
          { data }
        )
        .then((res) => {
          console.log(res.data);
        });
    },
    removeProduct(id) {
      this.$api
        .delete(
          `${this.$api.defaults.API}/offer/${this.offer.id}/product/${id}`
        )
        .then(() => {
          let index = this.products.findIndex((p) => p.id == id);
          this.products.splice(index, 1);
        });
    },
    sendToEmail() {
      const data = {
        send_to: this.data.email,
        co_format: this.data.tzType,
        required_tech_docs: this.data.tz,
        tech_docs_format: this.data.tech_docs_format,
        required_ru: this.data.ru,
      };
      this.$api
        .post(
          `${this.$api.defaults.API}/offer/${this.offer.id}/send-to-email`,
          data
        )
        .then(() => {
          (this.modalText = "Отправлено"), (this.openModal = true);
        });
    },
    saveOffer() {
      const products = [];
      if (this.edit) {
        if (this.offer.offer_products) {
          for (let p of this.offer.offer_products) {
            products.push({
              product: p.product,
              price: p.price,
              discount: p.discount,
              count: p.count,
              cost: p.cost,
            });
          }
        }
      }
      const data = {
        counterparty: this.currentCounterparty.id || null,
        end_customer: this.currentCustomer.id || null,
        company: this.data.company || null,
        offer_products: products || null,
        tech_docs_format: this.data.tech_docs_format,
      };
      // edit
      if (this.edit) {
        this.$api
          .patch(`${this.$api.defaults.API}/offer/${this.offer.id}`, data)
          .then(() => {
            (this.modalText = "Сохранено"), (this.openModal = true);
          });
      }
      // create
      else {
        this.$api.post(`${this.$api.defaults.API}/offer`, data).then(() => {
          (this.modalText = "Сохранено"), (this.openModal = true);
          for (const [key] of Object.entries(this.data)) {
            this.data[key] = "";
          }
        });
      }
    },
  },
};
</script>

