<template>
  <div
    class="base-select"
    v-click-outside="closeDropdown"
    :class="{ 'base-select-border': borderOnly }"
    ref="select"
  >
    <div
      class="base-select-selected"
      :class="[
        { open, disabled },
        {
          'base-select-selected-border': borderOnly,
          active: modelValue && modelValue !== '',
        },
      ]"
      @click="openDropdown"
    >
      <span v-if="!modelValue || (modelValue == '' && placeholder)">
        {{ placeholder }}
      </span>
      <span class="selected ellipse" v-else>
        {{ selected && selected.key }}
      </span>
    </div>
    <div
      v-if="!disabled && open"
      class="base-select-items"
      :class="{ 'base-select-items-border': borderOnly }"
      ref="dropdown"
      :style="dropdownDirection"
    >
      <div
        v-for="(option, i) of options"
        :key="`${option.value}_${i}`"
        :class="{ active: modelValue == option.value }"
        @click="
          selected = option;
          open = false;
          $emit('update:modelValue', option.value);
          $emit('change');
        "
      >
        {{ option.key }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    options: {
      type: Array,
      required: true,
    },
    label: {
      type: String,
      default: "",
    },
    placeholder: {
      type: [Number, String],
      default: "",
    },
    disabled: {
      type: Boolean,
    },
    borderOnly: {
      type: Boolean,
    },
    modelValue: {
      type: [String, Number],
    },
    yellowBg: {
      type: Boolean,
    },
  },
  computed: {
    selected() {
      return this.options.find((i) => i.value === this.modelValue) || "";
    },
  },
  data() {
    return {
      open: false,
      dropdownDirection: null,
    };
  },
  methods: {
    openDropdown() {
      if (!this.disabled) this.open = !this.open;
      if (this.open) {
        setTimeout(() => {
          let selectTop = this.$refs.select.getBoundingClientRect().top;
          let dropdownHeight =
            this.$refs.dropdown.getBoundingClientRect().height;
          if (window.innerHeight - selectTop < dropdownHeight + 60)
            this.dropdownDirection = `top: auto; bottom: ${
              this.borderOnly ? 23 : 40
            }px`;
          else this.dropdownDirection = `top: ${this.borderOnly ? 23 : 40}px`;
        });
      }
    },

    closeDropdown() {
      this.open = false;
    },
  },
  emits: ["update:modelValue", "change"],
};
</script>

<style scoped lang="scss">
.base-select {
  position: relative;
  width: 100%;
  height: 40px;
  text-align: left;
  outline: none;
  &-border {
    height: 25px;
  }
  &-selected {
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 16px 0 10px;
    border-radius: 5px;
    border: 1px solid $grayLighter;
    transition: 0.3s ease;
    cursor: pointer;
    user-select: none;
    .selected {
      width: calc(100% - 55px);
      position: absolute;
      font-family: $regular;
      font-size: 16px;
      line-height: 19px;
      text-align: left;
    }
    > svg {
      margin-right: 8px;
    }
    &:after {
      content: "";
      width: 24px;
      height: 14px;
      position: absolute;
      top: 14px;
      right: 10px;
      background-image: url(~@/assets/icons/dropdown-arrow.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      transition: 0.3s ease;
    }
    &.open,
    &.active {
      background: $surfaceHover;
      border-color: $surfaceHover;
    }
    &.open {
      &:after {
        transform: rotate(180deg);
      }
    }
    &.disabled {
      opacity: 0.5;
    }
    &-border {
      height: 25px;
      border: none;
      border-bottom: 2px solid $black;
      border-radius: 0;
      padding-left: 3px;
      transition: 0s;
      &.open {
        border: none;
        padding-bottom: 2px;
      }
      &.active,
      &.open {
        background: transparent;
        border-color: $black;
      }
      &:after {
        top: 4px;
        right: 0;
      }
      .selected {
        width: calc(100% - 25px);
      }
    }
  }
  &-items {
    max-height: 235px;
    position: absolute;
    left: 0;
    right: 0;
    background-color: $white;
    border-radius: 0px 0px 6px 6px;
    box-shadow: 0px 0px 4px rgba($black, 0.25);
    border-radius: 5px;
    padding: 8px 0;
    overflow: hidden;
    overflow-y: auto;
    z-index: 3;
    transition: 0.3s ease;

    > div {
      display: flex;
      align-items: center;
      font-size: 16px;
      line-height: 20px;
      padding: 8.5px 12px;
      cursor: pointer;
      user-select: none;
      line-break: anywhere;
      transition: 0.2s ease;
      &.active,
      &:hover {
        background: $grayHover;
      }
    }

    &::-webkit-scrollbar {
      width: 17px;
    }

    &::-webkit-scrollbar-thumb {
      background: $placeholder;
      border: 6px solid $white;
      border-radius: 150px;
    }

    &-border {
      width: 145px;
      left: -7.5px;
      > div {
        padding: 8.5px 11px;
      }
    }
  }
}
@media (max-width: 992px) {
  .base-select {
    &-selected {
      padding-left: 8px;
      &-border {
        padding-left: 0;
        span {
          margin-bottom: -7px;
        }
        &:after {
          top: 7px;
          right: -3px;
        }
      }
    }
    &-items {
      &-border {
        width: calc(100% + 20px);
        left: -10px;
      }
    }
  }
}
</style>
