    &::placeholder {
      line-height: 19px;
      color: $placeholder;
    }
    &:focus {
      border-color: $black;
    }
    &.error {
      color: $error;
      border-color: $error;
      &:focus {
        border-color: $error;
      }
    }
    &:disabled,
    &:read-only {
      &,
      &:focus {
        border-color: $grayLighter;
        background: $disabled;
      }
    }
  }
  &__msg {
    font-size: 12px;
    line-height: 15px;
    color: $error;
  }
  &-row {
    width: 563px;
    display: flex;
