    label {
      width: calc(100% - 380px);
      font-family: $regular;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      padding-top: 9px;
    }
    textarea {
      width: 380px;
    }
  }
}
@media (max-width: 992px) {
  .based-textarea {
    &-row {
      width: 100%;
      flex-direction: column;
